import { GetAllCustomersComponent } from './components/get-all-customers/get-all-customers.component';
import { GetOneCustomerComponent } from './components/get-one-customer/get-one-customer.component';
import { AddCouponComponent } from './components/add-coupon/add-coupon.component';
import { UpdateCouponComponent } from './components/update-coupon/update-coupon.component';
import { DeleteCouponComponent } from './components/delete-coupon/delete-coupon.component';
import { GetCompanyCouponsComponent } from './components/get-company-coupons/get-company-coupons.component';
import { GetCompanyCouponsByCategoryComponent } from './components/get-company-coupons-by-category/get-company-coupons-by-category.component';
import { GetCompanyCouponsByMaxPriceComponent } from './components/get-company-coupons-by-max-price/get-company-coupons-by-max-price.component';
