import { GetCustomerCouponByMaxPriceComponent } from './components/get-customer-coupon-by-max-price/get-customer-coupon-by-max-price.component';


const routes: Routes = [
  {path: "admin",component:AdminComponent},
  {path: "company",component:CompanyComponent},
  {path: "customer",component:CustomerComponent},
  {path: "getAllCoupons",component:GetAllCouponsComponent},
  {path: "login",component:LoginComponent},
  {path: "addCompany",component:AddCompanyComponent},
  {path: "updateCompany/:id",component:UpdateCompanyComponent}, //been updated with id
  {path: "deleteCompany/:id",component:DeleteCompanyComponent}, //been updated with id
  {path: "getAllCompanies",component:GetAllCompaniesComponent},
  {path: "getOneCompany/:id",component:GetOneCompanyComponent}, //been updated with id
  {path: "addCustomer",component:AddCustomerComponent},
  {path: "updateCustomer/:id",component:UpdateCustomerComponent},//been updated with id
  {path: "deleteCustomer/:id",component:DeleteCustomerComponent},//been updated with id
  {path: "getAllCustomers",component:GetAllCustomersComponent},
  {path: "getOneCustomer/:id",component:GetOneCustomerComponent},//been updated with id
  {path: "addCoupon",component:AddCouponComponent},
  {path: "updateCoupon/:id",component:UpdateCouponComponent},//been updated with id
  {path: "deleteCoupon/:id",component:DeleteCouponComponent},//been updated with id
  {path: "getCompanyCoupon",component:GetCompanyCouponsComponent},
  {path: "getCompanyCouponByCategory",component:GetCompanyCouponsByCategoryComponent},
  {path: "getCompanyCouponByMaxPrice",component:GetCompanyCouponsByMaxPriceComponent},
  {path: "getCompanyDetails",component:GetCompanyDetailsComponent},
  {path: "purchaseCoupon/:id",component:PurchaseCouponComponent},//been updated with id
  {path: "getCustomerCoupons",component:GetCustomerCouponsComponent},
  {path: "getCustomerCouponsByCategory",component:GetCustomerCouponsByCategoryComponent},
  {path: "getCustomerCouponsByMaxPrice",component:GetCustomerCouponByMaxPriceComponent},
  {path: "getCustomerDetails",component:GetCustomerDetailsComponent},
  {path:'',redirectTo:"login",pathMatch: "full"},
  {path: "**",component:PagenotfoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
