import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompaniesService } from 'src/app/services/companies.service';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-get-company-coupons-by-max-price',
  templateUrl: './get-company-coupons-by-max-price.component.html',
  styleUrls: ['./get-company-coupons-by-max-price.component.css']
})
export class GetCompanyCouponsByMaxPriceComponent implements OnInit {

  test:string = localStorage.typeL;
  price:number;
  coupons:Coupon[];
  constructor(private myService:CompaniesService,private myLogin:LoginService) { }

  ngOnInit() {
  }

  public getCouponByMaxPrice()
{
  this.myService.getCouponByMaxPrice(localStorage.token,this.price).subscribe( (c) =>
  {
    this.coupons = c;
    console.log("c:" + c.length)
  }, (error)=>{
    if(error.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
          alert("Time out. You have benn loggod off")
      }
      else
    alert("Search is empty . pick a price")
  })

}
}
