import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { AdminService } from 'src/app/services/admin.service';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-get-all-customers',
  templateUrl: './get-all-customers.component.html',
  styleUrls: ['./get-all-customers.component.css']
})
export class GetAllCustomersComponent implements OnInit {

  token:string = localStorage.token;
  customers:Customer[]
  constructor(private myService:AdminService,private myLogin:LoginService) { }

  ngOnInit() {
    this.myService.getAllCustomers(localStorage.token).subscribe( (c) =>
    {
      this.customers = c;
    }, (error)=>{
      if(error.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert("Time out. You have benn loggod off")
    })
  }

}
