import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { CustomersService } from 'src/app/services/customers.service';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-get-customer-details',
  templateUrl: './get-customer-details.component.html',
  styleUrls: ['./get-customer-details.component.css']
})
export class GetCustomerDetailsComponent implements OnInit {

  test:string = localStorage.typeL;
  customer:Customer;
  constructor(private myService:CustomersService,private myLogin:LoginService) { }

  ngOnInit() {
    this.myService.getCustomerDetails(localStorage.token).subscribe((c) =>
    {
      this.customer = c;
    }, (error)=>{
      if(error.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert("Time out. You have benn loggod off")
    })
  }

}
