import { Component, OnInit } from '@angular/core';
import { CouponsService } from 'src/app/services/coupons.service';
import { Coupon } from 'src/app/models/coupon';
import { CustomersService } from 'src/app/services/customers.service';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-get-all-coupons',
  templateUrl: './get-all-coupons.component.html',
  styleUrls: ['./get-all-coupons.component.css']
})
export class GetAllCouponsComponent implements OnInit {

  coupons: Coupon[];
  constructor(private myService:CustomersService,private myLogin:LoginService) { }

  ngOnInit() {
    this.myService.getAllCoupons(localStorage.token,).subscribe( (c) =>
    {
      this.coupons = c;
    }, (error)=>{
      if(error.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert("Time out. You have benn loggod off")
    })
  }

}
