import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { AdminService } from 'src/app/services/admin.service';
import { LoginService } from 'src/app/services/login.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-get-one-customer',
  templateUrl: './get-one-customer.component.html',
  styleUrls: ['./get-one-customer.component.css']
})
export class GetOneCustomerComponent implements OnInit {

  test:string = localStorage.typeL;
  id:number;
  customer:Customer
  constructor(private myService:AdminService,private myLogin:LoginService,private activatedRout:ActivatedRoute) { }

  ngOnInit() {
    this.id= this.activatedRout.snapshot.params.id
    this.getOneCustomer()
  }
  
  public getOneCustomer(){
    
    this.myService.getOneCustomer(localStorage.token,this.id).subscribe(c=>
    {
      this.customer=c;
     
    } ,err=>{
      if(err.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert("Time out. You have benn loggod off")
    }
    )
  }

}
