import { Component, OnInit } from '@angular/core';
import { LoginService } from 'src/app/services/login.service';
import { Login } from 'src/app/models/login';
import { ClientType } from 'src/app/models/client-type.enum';
import { RouterLink, Router } from '@angular/router';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  email:string;
  password:string;
  clientType:ClientType;
 
  constructor(private myService: LoginService,private router:Router) {}

  ngOnInit() {
  }
  public loginMethod(){
   this.myService.Login(this.email,this.password,this.clientType).subscribe(response=>{
    //alert(response) // alert("ok" + this.clientType);
      if(response.toString()=="Login Failed, check password or email")
      alert(response)
      else
      switch(this.clientType)
      {
        case ClientType.Administrator:localStorage.typeL="admin";localStorage.token=response; location.hash=('/admin'); location.reload() ;break;
        case ClientType.Company:localStorage.typeL="company"; localStorage.token=response;location.hash=('/company'); location.reload();break;
        case ClientType.Customer:localStorage.typeL="customer";localStorage.token=response;location.hash=('/customer'); location.reload() ;break;
        //location.href="http://localhost:4200/customer" \\location.href="http://localhost:4200/company" ; \\ location.href="http://localhost:4200/admin"
      }
    },err=>{
      alert("pick admin/customer/company")
    }
    )
  }
  public logoutMethod(){
    this.myService.Logout(localStorage.token).subscribe(response=>{
       alert("Goodbye");
       localStorage.token = null;
       localStorage.typeL="test";
       location.href="http://localhost:8080";
     },err=>{
       alert("Time out. You have benn loggod off")
     }
     )
   }

}
