import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Coupon } from '../models/coupon';
import { Observable } from 'rxjs';
import { Customer } from '../models/customer';

@Injectable({
  providedIn: 'root'
})
export class CustomersService {

  constructor(private httpClient: HttpClient) { }

  public purchaseCoupon(token:string,couponId:number){
    return this.httpClient.post("http://localhost:8080/customers/PurchaseCoupon/"+token+"/" + couponId,String
    );
  }
  public getCustomerCoupons(token:string):Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/customers/getCustomerCoupons/"+token);
  }
  public getCustomerCouponsCategory(token:string,category:number):Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/customers/getCustomerCouponsCategory/"+token+"/" + category);
  }
  public getCustomerCouponsMaxPrice(token:string,price:number):Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/customers/getCustomerCouponsMaxPrice/"+token+"/" + price);
  }

  public getCustomerDetails(token:string):Observable<Customer>{
    return this.httpClient.get<Customer>("http://localhost:8080/customers/getCustomerDetails/"+token);
  }
  public getAllCoupons(token:string):Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/customers/getAllCoupons/" + token);
  }


}
