import { Component, OnInit } from '@angular/core';
import { CompaniesService } from 'src/app/services/companies.service';
import { Coupon } from 'src/app/models/coupon';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-get-company-coupons',
  templateUrl: './get-company-coupons.component.html',
  styleUrls: ['./get-company-coupons.component.css']
})
export class GetCompanyCouponsComponent implements OnInit {

  test:string = localStorage.typeL;
  coupons:Coupon[];
  constructor(private myService:CompaniesService,private myLogin:LoginService) { }

  ngOnInit() {
    this.myService.getCompanyCoupons(localStorage.token).subscribe((c) =>
    {
      this.coupons = c;
    }, (error)=>{
      if(error.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert("Time out. You have benn loggod off")
    })
  }

}
