import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin.service';
import { Customer } from 'src/app/models/customer';
import { LoginService } from 'src/app/services/login.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-update-customer',
  templateUrl: './update-customer.component.html',
  styleUrls: ['./update-customer.component.css']
})
export class UpdateCustomerComponent implements OnInit {

  test:string = localStorage.typeL;
  id:number;
  firstName:string;
  lastName:string;
  email:string;
  password:string;
  fill:boolean;
  constructor(private router:Router,private myService: AdminService,private myLogin:LoginService,private activatedRout:ActivatedRoute) { }

  ngOnInit() {
    this.id=+ this.activatedRout.snapshot.params.id
    this.fill=true;
    this.fillInfo()
  }
  public updateCustomer(){
    let c:Customer;
     c = new Customer(this.id,this.firstName,this.lastName,this.email,this.password);
    this.myService.updateCustomer(localStorage.token,c).subscribe(c=>
    {
      alert("Update has been successful");
      this.fillOut();
      this.router.navigate(['/getAllCustomers'])
    } ,err=>{
      if(err.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert(err.error) 
    }
    )
  }
  public fillInfo()
  {
    this.myService.getOneCustomer(localStorage.token,this.id).subscribe(c=>
      {
      this.fill=true;
       this.firstName=c.firstName;
       this.lastName=c.lastName;
       this.email=c.email;
       this.password=c.password;

      } ,err=>{
        alert(err.error) //working but make this alert 
      }
      )

  } 
  public fillOut()
  {
    this.fill=false;
  }

}
