import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaderResponse, HttpHeaders} from '@angular/common/http';
import { Company } from '../models/company';
import { Customer } from '../models/customer';
import { Observable } from 'rxjs';
import { Content } from '@angular/compiler/src/render3/r3_ast';
@Injectable({
  providedIn: 'root'
})
export class AdminService {
  constructor(private httpClient: HttpClient) { }
  
  //company
  public addCompany(token:string,company:Company):Observable<Company>{
    return this.httpClient.post<Company>("http://localhost:8080/admin/addCompany/" + token,company
    );
  }
  public updateCompany(token:string,company:Company):Observable<Company>{
    return this.httpClient.put<Company>("http://localhost:8080/admin/updateCompany/"+token,company);
  }
  public deleteCompany(token:string,companyId:number){
    return this.httpClient.delete("http://localhost:8080/admin/deleteCompany/"+token+"/"+ companyId);
  }
  public getAllCompanies(token:string):Observable<Company[]>{
    return this.httpClient.get<Company[]>("http://localhost:8080/admin/getAllCompanies/"+token);
  }
  public getOneCompany(token:string,companyId:number):Observable<Company>{
    return this.httpClient.get<Company>("http://localhost:8080/admin/getOneCompany/"+token+"/"+ companyId);
  }

  //customer
  public addCustomer(token:string,customer:Customer):Observable<Customer>{
    return this.httpClient.post<Customer>("http://localhost:8080/admin/addCustomer/"+token,customer);
  }
  public updateCustomer(token:string,customer:Customer):Observable<Customer>{
    return this.httpClient.put<Customer>("http://localhost:8080/admin/updateCustomer/"+token,customer);
  }
  public deleteCustomer(token:string,customerId:number){
    return this.httpClient.delete("http://localhost:8080/admin/deleteCustomer/"+token+"/"+ customerId);
  }
  public getAllCustomers(token:string):Observable<Customer[]>{
    return this.httpClient.get<Customer[]>("http://localhost:8080/admin/getAllCustomers/"+token);
  }
  public getOneCustomer(token:string,customerId:number):Observable<Customer>{
    return this.httpClient.get<Customer>("http://localhost:8080/admin/getOneCustomer/"+token+"/"+ customerId);
  }


}
