import { Component, OnInit } from '@angular/core';
import { CompaniesService } from 'src/app/services/companies.service';
import { LoginService } from 'src/app/services/login.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-delete-coupon',
  templateUrl: './delete-coupon.component.html',
  styleUrls: ['./delete-coupon.component.css']
})
export class DeleteCouponComponent implements OnInit {

  test:string = localStorage.typeL;
  couponId:number;
  constructor(private router:Router,private myService:CompaniesService,private myLogin:LoginService,private activatedRout:ActivatedRoute) { }

  ngOnInit() {
    this.couponId= this.activatedRout.snapshot.params.id
  }
  public deleteCoupon(){
    
    this.myService.deleteCoupon(localStorage.token,this.couponId).subscribe(ref=>
    {
      alert("Deleted");
      this.couponId=null;
      this.router.navigate(['/getCompanyCoupon'])
    } ,err=>{
      if(err.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert(err.error) 
      // console.log(err )
    }
    )
  }

}
