import { Component, OnInit } from '@angular/core';
import { CustomersService } from 'src/app/services/customers.service';
import { LoginService } from 'src/app/services/login.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-purchase-coupon',
  templateUrl: './purchase-coupon.component.html',
  styleUrls: ['./purchase-coupon.component.css']
})
export class PurchaseCouponComponent implements OnInit {

  test:string = localStorage.typeL;
  CouponId:number;
  constructor(private router:Router,private MyService:CustomersService,private myLogin:LoginService,private activatedRout:ActivatedRoute) { }

  ngOnInit() {
    this.CouponId=this.activatedRout.snapshot.params.id
  }
  public purchaseCoupon()
  {
    this.MyService.purchaseCoupon(localStorage.token,this.CouponId).subscribe(c=>
      {
        alert("Purchase has been successful");
        this.CouponId=null;
        this.router.navigate(['/getAllCoupons'])
      },
      err=>
      {
        if(err.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
        alert(err.error);
      }
      )
  }

}
