import { Component, OnInit } from '@angular/core';
import { CompaniesService } from 'src/app/services/companies.service';
import { Coupon } from 'src/app/models/coupon';
import { Category } from 'src/app/models/category.enum';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-add-coupon',
  templateUrl: './add-coupon.component.html',
  styleUrls: ['./add-coupon.component.css']
})
export class AddCouponComponent implements OnInit {

  test:string = localStorage.typeL;
  category:Category;
  title:string;
  description:string;
  startDate:Date;
  endDate:Date;
  amount:number;
  price:number;
  image:string;
  

  constructor(private myService:CompaniesService,private myLogin:LoginService) { }

  ngOnInit() {
  }
  public addCoupon(){
   
    let c:Coupon;
  
     c = new Coupon(0,this.category,this.title,this.description,this.startDate,this.endDate,
      this.amount,this.price,this.image);
    this.myService.addCoupon(localStorage.token,c).subscribe(c=>
    {
      alert("Coupon added" );
      this.category=null;
      this.title=null;
      this.description=null;
      this.startDate=null;
      this.endDate=null;
      this.amount=null;
      this.price=null;
      this.image=null;
    } ,err=>{
      if(err.status===401)
    {
      this.myLogin.Logout(localStorage.token).subscribe(res=>
        {
          localStorage.token = null;
          localStorage.typeL="test";
          location.href="http://localhost:8080";
        })
    }
    else if(err.status!=200)
    {
      alert("U need to fill everthing or " + err.error ) 
    }
    else //error 200
    {
      this.category=null;
      this.title=null;
      this.description=null;
      this.startDate=null;
      this.endDate=null;
      this.amount=null;
      this.price=null;
      this.image=null;
    }
      //debug
      // console.log(err)
    }
    )
  }

}
