import { Component, OnInit, OnChanges, SimpleChange, SimpleChanges, Input } from '@angular/core';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit {

  test:string = localStorage.typeL;
  constructor(private myService:LoginService) { }

  ngOnInit() {
    
  }
  

  public logoutMethod(){
    this.myService.Logout(localStorage.token).subscribe(response=>{
       alert("Goodbye");
       localStorage.token = null;
       localStorage.typeL=null;
       location.href="http://localhost:8080";
     },err=>{
       alert(err.error)
     }
     )
   }
}
