import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CustomersService } from 'src/app/services/customers.service';
import { Category } from 'src/app/models/category.enum';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-get-customer-coupons-by-category',
  templateUrl: './get-customer-coupons-by-category.component.html',
  styleUrls: ['./get-customer-coupons-by-category.component.css']
})
export class GetCustomerCouponsByCategoryComponent implements OnInit {

  test:string = localStorage.typeL;
  category:Category;
  coupons:Coupon[];
  constructor(private myService:CustomersService,private myLogin:LoginService) { }

  ngOnInit() {
  }
  public getCouponByCategory()
{
  this.myService.getCustomerCouponsCategory(localStorage.token,this.category).subscribe( (c) =>
  {
    this.coupons = c;
  }, (error)=>{
    if(error.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
          alert("Time out. You have benn loggod off")
      }
      else
    alert("Search is empty pick a category")
  })
}
}
