import { Component, OnInit } from '@angular/core';
import { CompaniesService } from 'src/app/services/companies.service';
import { Company } from 'src/app/models/company';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-get-company-details',
  templateUrl: './get-company-details.component.html',
  styleUrls: ['./get-company-details.component.css']
})
export class GetCompanyDetailsComponent implements OnInit {

  test:string = localStorage.typeL;
  company:Company;
  constructor(private myService:CompaniesService,private myLogin:LoginService) { }

  ngOnInit() {
    this.myService.getCompanyDetails(localStorage.token).subscribe((c) =>
    {
      this.company = c;
    }, (error)=>{
      if(error.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert("Time out. You have benn loggod off")
    })
  }

}
