import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin.service';
import { Company } from 'src/app/models/company';
import { LoginComponent } from '../login/login.component';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-get-all-companies',
  templateUrl: './get-all-companies.component.html',
  styleUrls: ['./get-all-companies.component.css']
})
export class GetAllCompaniesComponent implements OnInit {

  companies: Company[];
  constructor(private myService:AdminService,private myLogin:LoginService) { }

  ngOnInit() {
    this.myService.getAllCompanies(localStorage.token).subscribe((c) =>
    {
      this.companies = c;
    }, (error)=>{
      this.myLogin.Logout(localStorage.token).subscribe(response=>
        {
          localStorage.token = null;
          localStorage.typeL="test";
          location.href="http://localhost:8080";
        })
          alert("Time out. You have benn loggod off")
    
       })
    }
    
}


