import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin.service';
import { Company } from 'src/app/models/company';
import { LoginService } from 'src/app/services/login.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-get-one-company',
  templateUrl: './get-one-company.component.html',
  styleUrls: ['./get-one-company.component.css']
})
export class GetOneCompanyComponent implements OnInit {

  test:string = localStorage.typeL;
  id:number;
  company:Company
  constructor(private myService:AdminService,private myLogin:LoginService,private activatedRout:ActivatedRoute) { }

  ngOnInit() {
    this.id= this.activatedRout.snapshot.params.id
    this.getOneCompany();

  }
  public getOneCompany(){
    
    this.myService.getOneCompany(localStorage.token,this.id).subscribe(c=>
    {
      this.company=c;
    } ,err=>{
      if(err.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
           
          })
          alert("Time out. You have benn loggod off")
      }
      else
      alert("Search is empty.")
    }
    )
  }

}
