import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CustomersService } from 'src/app/services/customers.service';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-get-customer-coupon-by-max-price',
  templateUrl: './get-customer-coupon-by-max-price.component.html',
  styleUrls: ['./get-customer-coupon-by-max-price.component.css']
})
export class GetCustomerCouponByMaxPriceComponent implements OnInit {

  test:string = localStorage.typeL;
  price:number;
  coupons:Coupon[];
  constructor(private myService:CustomersService,private myLogin:LoginService) { }

  ngOnInit() {
  }
  public getCouponByMaxPrice()
  {
    this.myService.getCustomerCouponsMaxPrice(localStorage.token,this.price).subscribe( (c) =>
    {
      this.coupons = c;
      console.log("c:" + c.length)
    }, (error)=>{
      if(error.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
          alert("Time out. You have benn loggod off")
      }
      else
      alert("Search is empty. pick a price")
    })
  
  }

}
