import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin.service';
import { LoginService } from 'src/app/services/login.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-delete-company',
  templateUrl: './delete-company.component.html',
  styleUrls: ['./delete-company.component.css']
})
export class DeleteCompanyComponent implements OnInit {

  test:string = localStorage.typeL;
  id:number;
  constructor(private router:Router,private myService:AdminService,private myLogin:LoginService,private activatedRout:ActivatedRoute) { }

  ngOnInit() {
    this.id=+ this.activatedRout.snapshot.params.id
  }
  public deleteCompany(){
    
    this.myService.deleteCompany(localStorage.token,this.id).subscribe(()=>{
       this.id=null,alert("Deleted")
       this.router.navigate(['/getAllCompanies']) //go back to the main table
      },
    err=>
    {
      if(err.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert(err.error)
      console.log(err)}
    )



   
  }

}
