import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Coupon } from '../models/coupon';
import { Observable } from 'rxjs';
import { Company } from '../models/company';
@Injectable({
  providedIn: 'root'
})
export class CompaniesService {

  constructor(private httpClient: HttpClient) { }

  public addCoupon(token:string,coupon:Coupon):Observable<Coupon>{
    return this.httpClient.post<Coupon>("http://localhost:8080/companies/addCoupon/"+token,coupon
    );
  }
  public updateCoupon(token:string,coupon:Coupon):Observable<Coupon>{
    return this.httpClient.put<Coupon>("http://localhost:8080/companies/updateCoupon/"+token,coupon);
  }
  public deleteCoupon(token:string,couponId:number){
    return this.httpClient.delete("http://localhost:8080/companies/deleteCoupon/"+token+"/"+ couponId);
  }
  public getCompanyCoupons(token:string):Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/companies/getCompanyCoupon/"+token);
  }
  public getCouponByCategory(token:string,category:number):Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/companies/getComapnyCouponCategory/"+token+"/" + category);
  }
  public getCouponByMaxPrice(token:string,price:number):Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/companies/getComapnyCouponMaxPrice/"+token+"/" + price);
  }

  public getCompanyDetails(token:string):Observable<Company>{
    return this.httpClient.get<Company>("http://localhost:8080/companies/getComapnyDetails/"+token);
  }

}
