import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin.service';
import { Customer } from 'src/app/models/customer';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: ['./add-customer.component.css']
})
export class AddCustomerComponent implements OnInit {

  test:string = localStorage.typeL;
  firstName:string;
  lastName:string
  email:string;
  password:string;
  constructor(private myService:AdminService,private myLogin:LoginService) { }

  ngOnInit() {
  }
  public addCustomer(){
    let c:Customer;
     c = new Customer(0,this.firstName,this.lastName,this.email,this.password);
    this.myService.addCustomer(localStorage.token,c).subscribe(c=>
    {
      alert("Customer added");
      this.firstName=null;
      this.lastName=null;
      this.email=null;
      this.password=null;
    } ,err=>{
      if(err.status===401)
    {
      this.myLogin.Logout(localStorage.token).subscribe(c=>
        {
          localStorage.token = null;
          localStorage.typeL="test";
          location.href="http://localhost:8080";
        })
    }
    else if(this.firstName==null || this.lastName==null || this.email==null)
    alert("values not good")
    else if(err.status===406)
      alert(err.error)
      console.log(err) 
    }
    )
  }

}
