import { Component, OnInit } from '@angular/core';
import { CompaniesService } from 'src/app/services/companies.service';
import { Coupon } from 'src/app/models/coupon';
import { Category } from 'src/app/models/category.enum';
import { LoginService } from 'src/app/services/login.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-update-coupon',
  templateUrl: './update-coupon.component.html',
  styleUrls: ['./update-coupon.component.css']
})
export class UpdateCouponComponent implements OnInit {

  test:string = localStorage.typeL;
  couponId:number;
  category:Category;
  title:string;
  description:string;
  startDate:Date;
  endDate:Date;
  amount:number;
  price:number;
  image:string;
  fill:boolean;
  constructor(private router:Router,private myService:CompaniesService,private myLogin:LoginService,private activatedRout:ActivatedRoute) { }

  ngOnInit() {
    this.couponId= this.activatedRout.snapshot.params.id
    this.fill=true;
    this.fillInfo();
  }
  public updateCoupon(){
    let c:Coupon;
    c = new Coupon(this.couponId,this.category,this.title,this.description,this.startDate,this.endDate,
     this.amount,this.price,this.image);
    this.myService.updateCoupon(localStorage.token,c).subscribe(c=>
    {
      alert("Update has been successful " );
      this.couponId=null;
      this.category=null;
      this.title=null;
      this.description=null;
      this.startDate=null;
      this.endDate=null;
      this.amount=null;
      this.price=null;
      this.image=null;
      this.fill=false;

      this.router.navigate(['/getCompanyCoupon'])
    } ,err=>{
      if(err.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert(err.error ) 
      // console.log(err)
    }
    )
  }
  public fillInfo()
  {
   this.myService.getCompanyCoupons(localStorage.token).subscribe(c=>
    {
      let i:number;
    for(i=0;i<c.length;i++)
    {
      if(c[i].id==this.couponId)
      {
      this.fill=true;
      this.category=c[i].category;
      this.title=c[i].title;
      this.description=c[i].description;
      this.startDate = c[i].startDate;
      this.endDate = c[i].endDate;
      this.amount=c[i].amount;
      this.price=c[i].price
      this.image=c[i].image;
      break;
      }
    }
    
    })
    
    
     
    
  }
    
  


}
