import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin.service';
import { Company } from 'src/app/models/company';
import { LoginService } from 'src/app/services/login.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-update-company',
  templateUrl: './update-company.component.html',
  styleUrls: ['./update-company.component.css']
})
export class UpdateCompanyComponent implements OnInit {

  test:string = localStorage.typeL;
  id:number;
  name:string;
  email:string;
  password:string;
  fill:boolean;
  constructor(private myService:AdminService,private myLogin:LoginService,private activatedRout:ActivatedRoute,private router:Router) { }

  ngOnInit() {
    this.id= this.activatedRout.snapshot.params.id
    this.fill=true;
    this.fillInfo();
  }
  public updateCompany(){
    let c:Company;
     c = new Company(this.id,this.name,this.email,this.password);
     if(c.email=="")
     {
      alert("email is empty")
      return;
     }
     if(c.password=="")
     {
       alert("password is empty")
      return;
     }
     
     
    this.myService.updateCompany(localStorage.token,c).subscribe(()=>
    {
      alert("Update has been successful");
      this.fillOut();
      this.router.navigate(['/getAllCompanies'])
      
    } ,err=>{
      if(err.status===401)
      {
        this.myLogin.Logout(localStorage.token).subscribe(res=>
          {
            localStorage.token = null;
            localStorage.typeL="test";
            location.href="http://localhost:8080";
          })
      }
      else
      alert(err.error) 
      
    }
    )
  }
  public fillInfo()
  {
    this.myService.getOneCompany(localStorage.token,this.id).subscribe(c=>
      {
      this.fill=true;
       this.name=c.name;
       this.email=c.email;
       this.password=c.password;

      } ,err=>{
        alert("id not found ") //working but make this alert 
      }
      )

  }
  
  public fillOut()
  {
    this.fill=false;
  }

}
